import Vue from 'vue'
import Vuex from 'vuex'
import request from '../network/index'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    list: [
      {
        id: 488267633,
        title: '',
        artist: 'BUMP OF CHICKEN',
        src: 'http://qwud2atsk.hn-bkt.clouddn.com/BUMP%20OF%20CHICKEN%20-%20%E8%A8%98%E5%BF%B5%E6%92%AE%E5%BD%B1.mp3',
        pic: 'https://p2.music.126.net/SurEwKIT6cDcB2dL1UQTXg==/19012755067719432.jpg',
        lrc: '[00:00.000]  : Motoo Fujiwara\n[00:01.000]  : Motoo Fujiwara\n[00:16.82]\n[00:20.75]\n[00:24.63] \n[00:28.71]\n[00:40.78] \n[00:44.71]\n[00:48.73] \n[00:52.73] \n[00:56.29]\n[01:04.45] \n[01:11.27] \n[01:15.92]\n[01:19.66] \n[01:23.83]\n[01:36.72]\n[01:40.75]\n[01:44.66] \n[01:48.76] \n[01:52.76] \n[01:56.67]\n[02:00.75] \n[02:04.85] \n[02:08.26]\n[02:11.93]\n[02:16.35] \n[02:20.02]\n[02:22.97] \n[02:27.94]\n[02:31.97] \n[02:36.71]\n[03:12.84]\n[03:16.64] \n[03:20.58] \n[03:24.69] \n[03:28.22] \n[03:36.24] \n[03:45.35]\n[03:46.36]\n[03:49.98]\n[03:54.15] \n[03:58.85]\n[04:02.65]\n[04:05.96]\n[04:09.95] \n[04:14.11]\n[04:17.89]\n'
      }
    ],
    userData: {
      userInfo: '',
      islogin: false
    }
  },
  mutations: {
    //  
    addMusic(state, item) {
      state.list = state.list.filter((i) => i.id !== item.id)
      state.list.unshift(item)
    },
    // 
    addMusic1(state, item) {
      state.list = state.list.filter((i) => i.id !== item.id)
      state.list.push(item)
    },
    // 
    clear(state) {
      state.list = []
    },
    addUserData(state, item) {
      state.userInfo = item
      state.islogin = true
    }
  },
  actions: {
    // 
    async getList(context, payload) {
      // const res = {}
      // console.log(payload)
      // payload={id,insert:true} true    false 
      const [{ data: data1 }, { data: data2 }, { data: data3 }] = await Promise.all([
        context.dispatch('getSrc', payload.id),
        context.dispatch('getInfo', payload.id),
        context.dispatch('getLyric', payload.id)
      ])
      console.log(data3)
      const lrc = data3.nolyric || data3.uncollected ? '[00:00.000]' : data3.lrc.lyric
      const res = {
        id: payload.id,
        title: data2.songs[0].name,
        artist: data2.songs[0].ar[0].name,
        src: data1.data[0].url,
        pic: data2.songs[0].al.picUrl,
        lrc
      }
      console.log(res)
      if (payload.insert) {
        context.commit('addMusic', res)
      } else {
        context.commit('addMusic1', res)
      }
    },
    // src  /song/url?id=33894312
    getSrc(context, id) {
      return new Promise((resolve, reject) => {
        request(`/song/url?id=${id}`).then(res => resolve(res), err => reject(err))
      })
    },
    //  title pic artist /song/detail?ids=347230
    getInfo(context, id) {
      return new Promise((resolve, reject) => {
        request(`/song/detail?ids=${id}`).then(res => resolve(res), err => reject(err))
      })
    },
    // 
    getLyric(context, id) {
      return new Promise((resolve, reject) => {
        request(`/lyric?id=${id}`).then(res => resolve(res), err => reject(err))
      })
    },
  },
  modules: {

  }
})
